<template>
    <div>
		<div class="page-name">
            <h2>Ответы</h2>
            <div class="add_object">
                <router-link :to="{name: 'answerAdd'}" class="btn btn-success">Добавить</router-link>
            </div>
        </div>

        <div class="form-delete">

			<div class="object-list">

				<div class="object x2">
					<div class="name">пользователи</div>
				</div>

				<div v-for="object in objects" :key="object.id"  :class="['product custom object x2']">
					<div class="name">
	                    <router-link :to="{name: 'answerEdit', params: { id: object.id }}" class="object_name" >{{ object.name }}</router-link>
					</div>
					<button type="submit" class="btn btn-danger button" @click.prevent="deleteObject(object.id)">Удалить</button>
				</div>
			</div>
    	</div>

    </div>
</template>


<script>
    export default {
        data: function(){
    		return {
    			objects: [],
    		}
    	},

    	mounted: function (){
    		this.$nextTick(function(){

    			let o = this;
    			axios.get(o.$store.state.domain + '/answers/get', {headers: {'Authorization': 'Bearer ' + o.$cookie.get('token')}}).then(function(response){
    				o.objects = response.data;
    			}).catch(function (error){
    				console.log(error);
    			});

    		})
    	},

    	methods: {

			deleteObject (id){

				let o = this;

				axios.get(o.$store.state.domain + '/answers/delete/' + id, {headers: {'Authorization': 'Bearer ' + o.$cookie.get('token')}}).then(function(response){
					for(let i=0; i<o.objects.length; i++){
						if(o.objects[i].id == id){
							o.objects.splice(i, 1);
							break;
						}
					}
				}).catch(function (error) {
					console.log(error);
				});

    		},

    	},
    }
</script>
