<template>
    <div>
        <h2>Изменение пользователя</h2>

		<div class="field-block">
			<div class="name">Выберите пользователя</div>
			<div class="wrap_select_block">
				<div class="select_block">
					<div class="active_select form-control">{{ role.text }}</div>
					<div class="change" @click="role.active =  !role.active"><i class="fas fa-sort-down"></i></div>
				</div>
				<div v-if="role.active " class="select_elements">
					<div v-for="object in role.objects" :key="object.id" @click="changeRole(object.id, object.name)" class="select_element">{{ object.name }}</div>
				</div>
			</div>
		</div>

		<div class="field-block">
			<pre v-html="name"></pre>
		</div>

		<div v-if="err" :class="['message', err ? 'alert-success' : 'alert-danger']">{{ message }}</div>

		<button type="submit" class="btn btn-primary sub" @click.prevent="editObject">Добавить</button>

    </div>
</template>


<script>
    export default {
        data: function(){
    		return {
				role: {
					id:			'',
					active: 	false,
					text: 		"Выберите пользователя",
					objects:	[],
				},

				name: 		"",
        		err:		false,
        		message:	"",
    		}
    	},

        mounted: function (){
    		this.$nextTick(function(){

    			let o = this;

				axios.get(o.$store.state.domain + '/users/get', {headers: {'Authorization': 'Bearer ' + o.$cookie.get('token')}}).then(function(response){
					o.role.objects = response.data;

	    			axios.get(o.$store.state.domain + '/answers/get/' + o.$route.params.id, {headers: {'Authorization': 'Bearer ' + o.$cookie.get('token')}}).then(function(response){

						for(let i=0; i<o.role.objects.length; i++){
							if(o.role.objects[i].id == response.data.uid){
								o.role.id 	= o.role.objects[i].id;
								o.role.text = o.role.objects[i].name;
								break;
							}
						}

						o.name 		= response.data.name;
						o.psw 		= response.data.password;

	    			}).catch(function (error){
	    				console.log(error);
	    			});

				}).catch(function (error){
					console.log(error);
				});

    		})
    	},

    	methods: {

    	}

    }
</script>
