<template>
    <div>
        <h2>Изменение роли</h2>

		<label for='name'>Наименование</label>
		<input v-model="name" id='name' class="form-control" name='name' type='text' />

		<div v-if="err" :class="['message', err ? 'alert-success' : 'alert-danger']">{{ message }}</div>

		<button type="submit" class="btn btn-primary sub" @click.prevent="editObject">Добавить</button>

    </div>
</template>


<script>
    export default {
        data: function(){
    		return {
				name: 		"",

        		err:		false,
        		message:	"",
    		}
    	},

        mounted: function (){
    		this.$nextTick(function(){

    			let o = this;
    			axios.get(o.$store.state.domain + '/roles/get/' + o.$route.params.id, {headers: {'Authorization': 'Bearer ' + o.$cookie.get('token')}}).then(function(response){
    				o.name = response.data.name;
    			}).catch(function (error){
    				console.log(error);
    			});

    		})
    	},

    	methods: {

    		editObject: function(){
    			let o    = this;
    			let data = new FormData();

    			data.append("name", o.name);

    			axios.post(o.$store.state.domain + '/roles/update/' + o.$route.params.id , data, {headers: {'Authorization': 'Bearer ' + o.$cookie.get('token')}}).then(function(response){

					if(response.data.response){
    					o.err 		= true;
    					o.message	= "Изменен!";
    				}else{
    					o.err 		= false;
    					o.message	= response.data.err;
    				}

    			}).catch(function (error){
    				o.err 		= false;
    				o.message	= "Неизвестная ошибка!";
    			})

    		}

    	}

    }
</script>
