import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

import axios from 'axios';
import cookie from 'vue-cookie'


export default new Vuex.Store({
	state: {
		
		domain: window.location.hostname == "localhost" ? "http://localhost" : "",

		user: {
			role: "",
			auth: false,
		},
	},

	mutations: {
		USER_UPDATE(state, data){
			state.user = data;
		},
	},

	actions: {
		USER_UPDATE: async ({commit, state}) => {
			let {data} = await axios.get(state.domain + '/auth/user/get', {headers: {'Authorization': 'Bearer ' + cookie.get('token')}});
			commit('USER_UPDATE', data);
		},
	}

})
