import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'

import SignIn from './views/auth/SignIn.vue'
import Registration from './views/auth/Registration.vue'

import Roles from './views/roles/Roles.vue';
import RoleAdd from './views/roles/RoleAdd.vue';
import RoleEdit from './views/roles/RoleEdit.vue';

import Users from './views/users/Users.vue';
import UserAdd from './views/users/UserAdd.vue';
import UserEdit from './views/users/UserEdit.vue';


import Answers from './views/answers/Answers.vue';
import AnswerAdd from './views/answers/AnswerAdd.vue';
import AnswerEdit from './views/answers/AnswerEdit.vue';


Vue.use(Router)

export default new Router({
	mode: 'history',
	base: process.env.BASE_URL,
 	routes: [
		{path: '/', name: 'home', component: Home},

		{path: '/user/sing-in', name: 'SignIn', component: SignIn},
		{path: '/user/registration', name: 'Registration', component: Registration},

		{path: '/roles', name: 'roles', component: Roles},
		{path: '/roles/add', name: 'roleAdd', component: RoleAdd},
		{path: '/roles/:id', name: 'roleEdit', component: RoleEdit},

		{path: '/users', name: 'users', component: Users},
		{path: '/users/add', name: 'userAdd', component: UserAdd},
		{path: '/users/:id', name: 'userEdit', component: UserEdit},

		{path: '/answers', name: 'answers', component: Answers},
		{path: '/answers/add', name: 'answerAdd', component: AnswerAdd},
		{path: '/answers/:id', name: 'answerEdit', component: AnswerEdit},

 	]
})
