<template>
	<div class="auth-wrap-form">

		<div class="auth-form">

			<div class="auth-name">Регистрация</div>

			<div :class="['auth-field-block', {active: fields.login.focused}]">
				<input v-model.trim="fields.login.name" @focus="fields.login.focused = true" @blur="fields.login.focused = false" class="auth-form-input" type='text' placeholder="Логин"/>
				<div v-if="fields.login.is_err" class="auth-err">{{ fields.login.err_name }}</div>
			</div>

			<div :class="['auth-field-block', {active: fields.passw.focused}]">
				<input v-model.trim="fields.passw.name"  @focus="fields.passw.focused = true" @blur="fields.passw.focused = false" class="auth-form-input" type='password' placeholder="Пароль"/>
				<div v-if="fields.passw.is_err" class="auth-err">{{ fields.passw.err_name }}</div>
			</div>

			<div :class="['auth-field-block', {active: fields.name.focused}]">
				<input v-model.trim="fields.name.name"  @focus="fields.name.focused = true" @blur="fields.name.focused = false" class="auth-form-input" type='text' placeholder="имя"/>
				<div v-if="fields.name.is_err" class="auth-err">{{ fields.name.err_name }}</div>
			</div>

			<div :class="['auth-field-block', {active: fields.surname.focused}]">
				<input v-model.trim="fields.surname.name"  @focus="fields.surname.focused = true" @blur="fields.surname.focused = false" class="auth-form-input" type='text' placeholder="фамилия"/>
				<div v-if="fields.surname.is_err" class="auth-err">{{ fields.surname.err_name }}</div>
			</div>

			<div :class="['auth-field-block', {active: fields.middle_name.focused}]">
				<input v-model.trim="fields.middle_name.name"  @focus="fields.middle_name.focused = true" @blur="fields.middle_name.focused = false" class="auth-form-input" type='text' placeholder="отчество"/>
				<div v-if="fields.middle_name.is_err" class="auth-err">{{ fields.middle_name.err_name }}</div>
			</div>


			<!--
			<div :class="['auth-field-block', {active: fields.email.focused}]">
				<input v-model.trim="fields.email.name"  @focus="fields.email.focused = true" @blur="fields.email.focused = false" class="auth-form-input" type='text' placeholder="Email"/>
				<div v-if="fields.email.is_err" class="auth-err">{{ fields.email.err_name }}</div>
			</div>
			-->

			<div @click="registration" class="auth-form-button">Регистрация</div>

			<div v-if="error.is" class="auth-global-err">{{ error.name }}</div>

		</div>

	</div>
</template>

<script>

import axios from 'axios';

export default {

	data: function(){
		return {

			fields: {
				login: {
					name: "",
					focused: false,
					err_name: "",
					is_err: false,
				},

				passw: {
					name: "",
					focused: false,
					err_name: "",
					is_err: false,
				},

				name: {
					name: "",
					focused: false,
					err_name: "",
					is_err: false,
				},

				surname: {
					name: "",
					focused: false,
					err_name: "",
					is_err: false,
				},

				middle_name: {
					name: "",
					focused: false,
					err_name: "",
					is_err: false,
				},

				/*
				email: {
					name: "",
					focused: false,
					err_name: "",
					is_err: false,
				},
				*/

			},

			error: {
				is: false,
				name: "",
			},

		}
	},

	watch: {

		"fields.login.name": function(val){
			if(val.length <= 0 && val.length > 16){
				this.fields.login.is_err = true;
				this.fields.login.err_name = "Поле логин не должно превышать 16 символов.";
			}else{
				this.fields.login.is_err = false;
				this.fields.login.err_name = "";
			}
		},

		"fields.passw.name": function(val){
			if(val.length > 0){
				this.fields.passw.is_err = false;
				this.fields.passw.err_name = "";
			}
		},

		"fields.name.name": function(val){
			if(val.length > 0){
				this.fields.name.is_err = false;
				this.fields.name.err_name = "";
			}
		},

		"fields.surname.name": function(val){
			if(val.length > 0){
				this.fields.surname.is_err = false;
				this.fields.surname.err_name = "";
			}
		},

		"fields.middle_name.name": function(val){
			if(val.length > 0){
				this.fields.middle_name.is_err = false;
				this.fields.middle_name.err_name = "";
			}
		},

		/*
		"fields.email.name": function(val){

			const _emailRegex = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;

			if(_emailRegex.test(val)){
				this.fields.email.is_err = false;
				this.fields.email.err_name = "";
			}else{
				this.fields.email.is_err = true;
				this.fields.email.err_name = "Введите валидный email.";
			}

		},
		*/

	},

	methods: {

		registration(){

			let o = this;

			if(!o.fields.login.name.length){
				o.fields.login.is_err = true;
				o.fields.login.err_name = "Поле логин не должно быть пустым.";
				return false;
			}

			if(!o.fields.passw.name.length){
				o.fields.passw.is_err = true;
				o.fields.passw.err_name = "Поле пароль не должно быть пустым.";
				return false;
			}

			if(!o.fields.name.name.length){
				o.fields.name.is_err = true;
				o.fields.name.err_name = "Поле имя не должно быть пустым.";
				return false;
			}

			if(!o.fields.surname.name.length){
				o.fields.surname.is_err = true;
				o.fields.surname.err_name = "Поле фамилия не должно быть пустым.";
				return false;
			}

			if(!o.fields.middle_name.name.length){
				o.fields.middle_name.is_err = true;
				o.fields.middle_name.err_name = "Поле отчество не должно быть пустым.";
				return false;
			}

			/*
			if(!o.fields.email.name.length){
				o.fields.email.is_err = true;
				o.fields.email.err_name = "Введите валидный email.";
				return false;
			}
			*/

			let data    = new FormData();
			data.append("name", o.fields.login.name);
			data.append("psw", o.fields.passw.name);

			data.append("name", o.fields.name.name);
			data.append("surname", o.fields.surname.name);
			data.append("middle_name", o.fields.middle_name.name);

			//data.append("email", o.fields.email.name);

			axios.post(o.$store.state.domain + '/auth/user/registration', data).then(function(response){

				if(response.data.is){

					o.error.is = true;
					o.error.name = response.data.name;

				}else{
					o.$router.push({ name: 'SignIn'});
				}

			}).catch(function (error){
				console.log(error);
			});

		},

	},


}
</script>

<style scoped>

.auth-wrap-form {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
}

.auth-form {
    max-width: 320px;
    width: 100%;
    background: white;
    padding: 1em;
	border-radius: .25em;
	box-shadow: 0 0 10px black;
}

.auth-form .auth-name {
	font-size: 2em;
	margin-bottom: .5em;
	text-align: center;
	color: #2d2d2d;
}

.auth-field-block {
    margin-bottom: 1em;
    padding: 1em;
    background: #1d1b15;
    border-radius: .25em;
}

.auth-field-block.active {
    box-shadow: 0 0 0px 2px #228e5d inset;
}

.auth-field-block .auth-form-input {
    background: none;
    border: none;
	width: 100%;
}

.auth-form-button {
	background: #228e5d;
	padding: 1em;
	border-radius: .25em;
	text-transform: uppercase;
	text-align: center;
	letter-spacing: .15em;
	cursor: pointer;
	color: white;
}

.auth-form-button.registration {
    margin-top: 1em;
    background: #578acb;
}

.auth-form-input {
    color: white;
}

input[type="text"]::placeholder {color: white;}
input[type="text"]::-moz-placeholder {color: white;}
input[type="text"]::-webkit-input-placeholder {color: white;}
input[type="text"]:-ms-input-placeholder {color: white;}
input[type="text"]::-ms-input-placeholder {color: white;}

.auth-err {
    background: #844;
    padding: 1em;
    font-size: .8em;
    border-radius: .25em;
    position: absolute;
	color: white;
}

.auth-global-err {
    background: #844;
    padding: 1em;
    border-radius: .25em;
    font-size: .8em;
    margin-top: 1em;
    text-align: center;
	color: white;
}

</style>
