<template>
    <div>
        <h2>Добавление вопроса</h2>

		<div class="field-block">
	        <div class="name">Выберите пользователя</div>
	        <div class="wrap_select_block">
	            <div class="select_block">
	                <div class="active_select form-control">{{ role.text }}</div>
	                <div class="change" @click="role.active =  !role.active"><i class="fas fa-sort-down"></i></div>
	            </div>
	            <div v-if="role.active " class="select_elements">
	                <div v-for="object in role.objects" :key="object.id" @click="changeRole(object.id, object.name)" class="select_element">{{ object.name }}</div>
	            </div>
	        </div>
		</div>

		<div class="field-block">
	        <label for='name'>Наименование</label>
			<input v-model="name" class="form-control" type='text' />
		</div>

		<div v-if="err" :class="['message', err ? 'alert-success' : 'alert-danger']">{{ message }}</div>

		<button type="submit" class="btn btn-primary sub" @click.prevent="addObject">Добавить</button>

    </div>
</template>


<script>
    export default {
        data: function(){
    		return {

				role: {
					id:			'',
					active: 	false,
					text: 		"Выберите пользователя",
					objects:	[],
				},

                name: 		"",

        		err:		false,
        		message:	"",

    		}
    	},

		mounted: function(){
			this.$nextTick(function(){

				let o = this;
				axios.get(o.$store.state.domain + '/users/get', {headers: {'Authorization': 'Bearer ' + o.$cookie.get('token')}}).then(function(response){
					o.role.objects = response.data;
				}).catch(function (error){
					console.log(error);
				})

			})
		},

        methods: {

			changeRole: function(id, name){
				this.role.id = id;
				this.role.active = false;
				this.role.text = name;
			},

    		addObject: function(){

    			let o    = this;
    			let data = new FormData();

				data.append("uid", o.role.id);
    			data.append("name", o.name);

    			axios.post(o.$store.state.domain + window.location.pathname, data, {headers: {'Authorization': 'Bearer ' + o.$cookie.get('token')}}).then(function(response){

    				if(response.data.response){
    					o.err 		= true;
    					o.message	= "Добавлен!";
    				}else{
    					o.err 		= false;
    					o.message	= response.data.err;
    				}

    			}).catch(function (error){
    				o.err 		= false;
    				o.message	= "Неизвестная ошибка!";
    			})

    		}

    	}

    }
</script>
