<template>
	<div class="auth-wrap-form">

		<div class="auth-form">

			<div class="auth-name">Аутентификация</div>

			<div :class="['auth-field-block', {active: fields.login.focused}]">
				<input v-model.trim="fields.login.name" @focus="fields.login.focused = true" @blur="fields.login.focused = false" class="auth-form-input" type='text' placeholder="Логин"/>
				<div v-if="fields.login.is_err" class="auth-err">{{ fields.login.err_name }}</div>
			</div>

			<div :class="['auth-field-block', {active: fields.passw.focused}]">
				<input v-model.trim="fields.passw.name" @focus="fields.passw.focused = true" @blur="fields.passw.focused = false" class="auth-form-input" type='password' placeholder="Пароль"/>
				<div v-if="fields.passw.is_err" class="auth-err">{{ fields.passw.err_name }}</div>
			</div>

			<div @click="auth" class="auth-form-button">Войти</div>

			<div v-if="error.is_err" class="auth-global-err">{{ error.name }}</div>

			<router-link :to="{ name: 'Registration'}" class="auth-form-button registration">Зарегистрироваться</router-link>

		</div>

	</div>
</template>

<script>

import axios from 'axios';

export default {

	data: function(){
		return {

			fields: {
				login: {
					name: "",
					focused: false,
					err_name: "",
					is_err: false,
				},

				passw: {
					name: "",
					focused: false,
					err_name: "",
					is_err: false,
				},
			},

			error: {
				is_err: false,
				name: "",
			},

		}
	},

	watch: {
		"fields.login.name": function(val){
			if(val.length > 16){
				this.fields.login.is_err = true;
				this.fields.login.err_name = "Поле логин не должно превышать 16 символов.";
			}else{
				this.fields.login.is_err = false;
				this.fields.login.err_name = "";
			}
		},

		"fields.passw.name": function(val){
			if(val.length > 0){
				this.fields.passw.is_err = false;
				this.fields.passw.err_name = "";
			}
		},

	},

	methods: {

		auth(){

			let o = this;

			if(!o.fields.login.name.length){
				o.fields.login.is_err = true;
				o.fields.login.err_name = "Поле логин не должно быть пустым.";
				return false;
			}

			if(!o.fields.passw.name.length){
				o.fields.passw.is_err = true;
				o.fields.passw.err_name = "Поле пароль не должно быть пустым.";
				return false;
			}

			let data    = new FormData();
			data.append("name", o.fields.login.name);
			data.append("psw", o.fields.passw.name);

			axios.post(o.$store.state.domain + '/auth/sign-in', data).then(function(response){

				if(response.data.is_auth){
					o.$cookie.set("token", response.data.token);
					o.$store.dispatch('USER_UPDATE');
					o.$router.push({ name: 'home' });

				}else{
					o.error.is_err = true;
					o.error.name = response.data.err;
				}

			}).catch(function (error){
				console.log(error);
			});

		},

	},


}
</script>

