<template>
    <div>
        <h2>Изменение пользователя</h2>

		<div class="field-block">
			<div class="name">Выберите роль</div>
			<div class="wrap_select_block">
				<div class="select_block">
					<div class="active_select form-control">{{ role.text }}</div>
					<div class="change" @click="role.active =  !role.active"><i class="fas fa-sort-down"></i></div>
				</div>
				<div v-if="role.active " class="select_elements">
					<div v-for="object in role.objects" :key="object.id" @click="changeRole(object.id, object.name)" class="select_element">{{ object.name }}</div>
				</div>
			</div>
		</div>

		<div class="field-block">
	        <label for='name'>логин</label>
			<input v-model="username" class="form-control" type='text' />
		</div>

		<div class="field-block">
			<label for='name'>Пароль</label>
			<input v-model="psw" class="form-control" type='text' />
		</div>

		<div class="field-block">
			<label for='name'>Имя</label>
			<input v-model="name" class="form-control" type='text' />
		</div>

		<div class="field-block">
			<label for='name'>Фамилия</label>
			<input v-model="surname" class="form-control" type='text' />
		</div>

		<div class="field-block">
			<label for='name'>Отчество</label>
			<input v-model="middle_name" class="form-control" type='text' />
		</div>

		<div v-if="err" :class="['message', err ? 'alert-success' : 'alert-danger']">{{ message }}</div>

		<button type="submit" class="btn btn-primary sub" @click.prevent="editObject">Добавить</button>

    </div>
</template>


<script>
    export default {
        data: function(){
    		return {
				role: {
					id:			'',
					active: 	false,
					text: 		"Выберите роль",
					objects:	[],
				},

				username: 		"",
				psw:		"",

				name: "",
				surname: "",
				middle_name: "",


        		err:		false,
        		message:	"",
    		}
    	},

        mounted: function (){
    		this.$nextTick(function(){

    			let o = this;

				axios.get(o.$store.state.domain + '/roles/get', {headers: {'Authorization': 'Bearer ' + o.$cookie.get('token')}}).then(function(response){
					o.role.objects = response.data;

	    			axios.get(o.$store.state.domain + '/users/get/' + o.$route.params.id, {headers: {'Authorization': 'Bearer ' + o.$cookie.get('token')}}).then(function(response){

						for(let i=0; i<o.role.objects.length; i++){
							if(o.role.objects[i].id == response.data.rid){
								o.role.id 	= o.role.objects[i].id;
								o.role.text = o.role.objects[i].name;
								break;
							}
						}

						o.username = response.data.username;
						o.psw = response.data.password;
						o.name = response.data.name;
						o.surname = response.data.surname;
						o.middle_name = response.data.middle_name;

	    			}).catch(function (error){
	    				console.log(error);
	    			});

				}).catch(function (error){
					console.log(error);
				});

    		})
    	},

    	methods: {

    		editObject: function(){
    			let o    = this;
    			let data = new FormData();

    			data.append("name", o.name);
				data.append("psw", o.psw);


    			axios.post(o.$store.state.domain + '/users/update/' + o.$route.params.id , data, {headers: {'Authorization': 'Bearer ' + o.$cookie.get('token')}}).then(function(response){

					if(response.data.response){
    					o.err 		= true;
    					o.message	= "Изменен!";
    				}else{
    					o.err 		= false;
    					o.message	= response.data.err;
    				}

    			}).catch(function (error){
    				o.err 		= false;
    				o.message	= "Неизвестная ошибка!";
    			})

    		}

    	}

    }
</script>
