<template>
	<div v-if="$store.state.user.role == 'user'" class="questions">

		<div v-for="object in objects" :key="objects.id" class="field-block">

			<label for='name'>
				{{ object.name }}?

				<div v-for="(time, i) in object.time" :key="i">
					{{ time }}
				</div>

			</label>

			<input v-model="object.answer" class="form-control" type='text' />
		</div>

		<div class="field-block">
			<label for='name'>Есть ли у Вас учеба или работа?</label>

			<div class="my-custom-select">
				<button type="submit" class="btn btn-success" @click.prevent="customAnswer = true">да</button>
				<button type="submit" class="btn btn-danger" @click.prevent="customAnswer = false">нет</button>
			</div>

			<div v-if="customAnswer" class="my-custom-answer">
				<div class="field-block">
					<label for='name'>Введите время! Пример: 11:00-12:00 (Часовой диапазон)</label>
					<input v-model="customAnswerName" class="form-control" type='text' />
				</div>
			</div>

		</div>

		<div v-if="error.name" :class="['message', error.is ? 'alert-danger' : 'alert-success']">{{ error.name }}</div>

		<button type="submit" class="btn btn-primary sub" @click.prevent="addObject">Добавить</button>

		<pre v-html="result" class="pre">

		</pre>

	</div>
</template>
<script>

export default {

	data: function(){
		return {

			customAnswer: false,
			customAnswerName: "",

			objects: [

				{id: 1, name: 'Любимый вид спорта', answer: "", time: [
					"08:00-09:00", "09:00-10:00",
				]},

				{id: 2, name: 'Любимая телепередача', answer: "", time: [
					"10:00-11:00", "11:00-12:00",
				]},

				{id: 3, name: 'Любимое блюдо', answer: "", time: [
					"12:00-13:00", "13:00-14:00",
				]},

				{id: 4, name: 'Любимый писатель', answer: "", time: [
					"14:00-15:00", "15:00-16:00",
				]},

				{id: 5, name: 'Любимый актер', answer: "", time: [
					"16:00-17:00", "17:00-18:00",
				]},

				{id: 6, name: 'Любимый режиссер', answer: "", time: [
					"18:00-19:00", "19:00-20:00", "20:00-21:00", "21:00-22:00",
				]},

			],

			error: {
				is: false,
				name: "",
			},

			result: "",

		}
	},

	methods: {

		random: function (max) {
		  return Math.floor(Math.random() * Math.floor(max));
		},

		addObject: function(){

			let o = this;


			let content = "";

			for(let object of o.objects){

				if(object.answer.id == 7 && !object.answer.length) continue;

				if(!object.answer.length){
					o.error.is = true;
					o.error.name = "Одно из полей не заполненно, все поля обязательны к заполнению!";
					return false;
				}

				let flag = false;
				for(let time of object.time){
					if(o.customAnswerName == time){
						flag = true;
					}
				}

				if(flag){
					continue;
				}

				//let temp = object.id == 6 ? o.random(2) : o.random(4);

				content += object.name + ": " + object.answer + ", " + object.time[object.id == 6 ? o.random(4) : o.random(2)] + "\n\n";

			}

			if(o.customAnswer){
				content += "Время учебы или работы: " + o.customAnswerName + "\n\n";
			}


			o.error.is = false;
			o.error.name = "";


			let data = new FormData();

			data.append("name", content);

			axios.post(o.$store.state.domain + "/answers/user/add", data, {headers: {'Authorization': 'Bearer ' + o.$cookie.get('token')}}).then(function(response){

				for(let object of o.objects){
					object.answer = "";
				}

				o.error.is = false;
				o.error.name = "Ваш ответ успешно добавлен!";

				o.result = content;


			}).catch(function (error){
				console.log(error);
			})

		}

	},

}
</script>

<style scoped>

.my-custom-select {
    display: flex;
    align-items: center;
}

.my-custom-select .btn {
    margin-right: 1em;
    max-width: 320px;
}

</style>
